@import url(https://fonts.googleapis.com/css2?family=Questrial&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: left;
  height: 100vh;
  font-family: 'Questrial', sans-serif;
}
* {
  box-sizing: border-box;
}
.information, .mapInformation {
  height: 92vh;
  width: 100%;
  overflow: scroll;
}




/* Home page CSS */
.nav {
  display: none;
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  height: 8vh;
  position: absolute;
  top: 0;
  background-color: white;
  width: 100%;
}
h2 {
  padding: 0 10px;
  font-size: 24px;
}
h2 span {
  font-size: 18px;
  color: #A8D2A9;
}
.importIcon {
  /* position: absolute;
  top: 9vh;
  right: 0; */
  width: 24px;
  margin: 0 18px;
}
.helpIcon {
  width: 24px;
  margin-right: 15px;
}
.instructions {
  margin-top: 8vh;
  height: 84vh;
  width: 100%;
  position: absolute;
  background-color:rgba(211,211,211, 0.5);
  z-index: 4;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.instructions h4 {
  margin: 5px 0;
  font-size: 20px;
}
.instructions ol {
  background-color: white;
  margin: 0 10px;
  border-radius: 10px;
  padding: 20px 15px 20px 35px;
  padding-right: 15px;
  box-shadow: 2px 2px 2px 2px gray;
}
.welcome h2 {
  display: none;
}
.welcome {
  height: 50vh;
  background-image: url("https://res.cloudinary.com/dwbqzx4rr/image/upload/v1585637806/maps_yixbos.png");
  background-position: center;
  background-size: cover;
  color: black;
  text-align: center;
  font-size: 28px;
  margin: 40px 0 0 0;
}
.welcome h4 {
  display: flex;
  justify-content: center;
  padding: 0 20px;
  line-height: 1.3em;
  margin: 0;
  height: 50vh;
  display: flex;
  align-items: center;
  background-color: rgba(255,255,255,0.5)
}
nav {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 8vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 1;
  background-color: white;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.5);
}
nav a {
  text-decoration: none;
  color: black;
  font-size: 14px;
  text-align: center;
  flex: 1 1;
}
.logo{
  height: 18px;
  width: 15px;
}
.markerLogo {
  margin-top: 10px;
  width: 18px;
  height: 18px;
  border: 2px solid black;
  border-radius: 50% 50% 50% 0;
  transform: rotate(-45deg);
  background-color: white;
  padding-bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
ol {
  font-size: 16px;
  line-height: 1em;
  margin-right: 15px;
}
ol li {
  padding: 5px 0;
  
}
.homeForm {
  margin: 15px 25px;
  border: 2px solid rgba(0, 0, 0, 0.1);
}
.homeForm form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.titleList {
  display: flex;
  justify-content: space-between
}
.titleList h3 {
  padding-top: 5px;
}
.addPerson {
  width: 25px;
  height: 25px;
  margin: 15px 20px;
}
.homeForm ul {
  list-style-type: none;
  padding: 5px 20px;
  margin: 0;
}
.listItem {
  padding-bottom: 10px;
  margin-left: 0;
}
.formName {
  height: 24px;
  border-radius: 20px;
}
.formAddress {
  height: 24px;
  border-radius: 20px;
}
input {
  border: 1px solid transparent;
  background-color: white;
  width: 80vw;
  height: 36px;
  margin: 10px 20px;
  padding: 0 12px;
  border-radius: 3px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  font-size: 14px;
  outline: none;
  z-index: 1;
}
button {
  font-size: 14px;
  margin: 15px;
  border: 2px solid black;
  border-radius: 20px;
  width: 100px;
  height: 25px;
  background-color: white;
  margin: 10px 35% 25px;
}
.alert {
  color:#E17206;
  font-size: 14px;
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
}
.checkMark {
  width: 12px;
  display: inline;
}
.listItem {
  display: flex;
  justify-content: start;
}
.removeIcon{
  margin-right: 5px;
  margin-left: 5px;
  width: 15px;
  height: 15px;
}
.removeIcon:hover {
  cursor: pointer;
}
h3  {
  color: #A8D2A9;
  text-align: left;
  margin: 10px 25px;
}
footer {
  display: none;
}
@media (min-width: 768px){
  h2 {
    font-size: 34px;
  }
  h2 span {
    font-size: 24px;
  }
  .helpIcon {
    width: 34px;
    height: 34px;
    top: 20px;
  }
  .importIcon {
    width: 35px;
  }
  .instructions h4 {
    font-size: 30px;
    padding-bottom: 10px;
  }
  .instructions ol {
    margin: 0 30px;
  }
  .instructions ol li {
    line-height: 1.5em;
    font-size: 24px;
  }
  .welcome {
    height: 60vh;
  }
  .welcome h4 {
    height: 60vh;
    font-size: 44px;
  }
  .titleList h3 {
    font-size: 24px;
  }
  .addPerson {
    width: 30px;
    height: 30px;
  }
  .formName {
    height: 32px;
    font-size: 18px;
  }
  .formAddress {
    height: 32px;
    font-size: 18px;
  }
  button {
    height: 32px;
    font-size: 18px;
  }
  .homeForm ul {
    font-size: 20px;
  }
  .alert {
    font-size: 16px;
  }
  .removeIcon {
    width: 20px;
    height: 20px;
  }
  nav a {
    font-size: 20px;
  }
  .logo {
    height: 24px;
    width: 20px;
  }
}
@media (min-width: 1024px){
  .information {
    height: 100vh;
  }
  nav {
    display: none;
  }
  .nav {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    top: 30vh;
    right: 5px;
    width: 4vw;
    background-color: white;
    border: 1px solid transparent;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    border-radius: 50px;
    margin: 5px;
    padding: 0;
  }
  .logo {
    width: 35px;
    height: 40px;
  }
  .nav a {
    text-decoration: none;
    font-weight: bold;
    font-size: 24px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-self: center;
    color: black;
    padding: 30px 30px;
    overflow: hidden;
  }
  .helpIcon {
    width: 34px;
    height: 34px;
  }
  .helpIcon:hover{
    cursor: pointer;
  }
  .importIcon:hover {
    cursor: pointer;
  }
 
  .instructions {
    height: 92vh;
  }
  main {
    display: flex;
  }
  .welcome {
    margin-top: 8vh;
    height: 80vh;
    flex: 1 1;
  }
  .welcome h4 {
    height: 80vh;
    font-size: 60px;
  }
  .allLists {
    margin-top: 8vh;
    display: flex;
    flex-direction: column;
    justify-content: start;
    margin-right: 5vw;
  }
  .homeForm {
    width: 40vw;
  }
  .formName {
    width: 30vw;
  }
  .formAddress {
    width: 30vw;
  }
  footer {
    display: block;
    margin-top: 8vh;
    padding: 20px;
    font-size: 18px;
    background-color: black;
    color: white;
  }
}
/* List page CSS  */
.listInformation header {
  visibility: hidden;
}
.list {
  width: 100%;
  height: 84vh;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
}
.list h4 {
  margin: 15px 0;

}
.fullList {
  margin: 15px 0;
  width: 300px;
  /* display: flex;
  flex-direction: column; */
  background-color: white;
  border: 1px solid transparent;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}
.fullList h4{
  padding: 0;
  margin: 10px;
  text-decoration: underline;
} 

.list h2{
  color: #A8D2A9;
  padding-top:20px;
  text-align: center;
}
.assign {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}
.fullList ul {
  list-style-type: none;
  padding: 0;
  margin-top: 0;
  /* margin-left: 5px; */
  text-align: left;
}
.fullList ul li {
  margin: 0 10px;
  padding: 20px 0px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px){
  .list h2 {
    font-size: 36px;
  }
  .list h3 {
    font-size: 32px;
    padding: 20px 0;
  }
  .fullList {
    width: 600px;
  }
  .fullList h4 {
    font-size: 24px;
  }
  .assign ul li {
    font-size: 20px;
  }
}
@media (min-width: 1024px){
  .assign {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
  .fullList {
    width: 35vw;
    margin: 50px;
    align-self: start;
  }
  .fullList h4 {
    font-size: 28px;
  }
  .assign ul li {
    font-size: 20px;
  }
}
/* SimpleMap CSS */
  .mapInformation Header {
    visibility: hidden;
  }
  hr {
    margin: 0;
    margin-bottom: 3px;
  }
  p {
    line-height: 1.5em;
    padding: 0;
    margin: 0;
  }

  h5 {
    font-weight: normal;
    font-size: 12px;
    margin: 0;
  }
  span {
    font-size: 14px;
    display: inline;
    font-weight: bold;
  }
  .assignDriver {
    display: flex;
    justify-content: space-between;
    align-items: start;
  }
  .plusSign {
    height: 20px;
    width: 20px;
    border: 2px solid black;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
  }
  .allDrivers {
    position: absolute;
    height: 92vh;
    width: 100%;
    background-color:rgba(211,211,211, 0.5);
    color: black;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .exit {
    position: absolute;
    top: 0%;
    right: 0%;
    color: #696969;
    font-size: 40px;
    padding: 20px;
  }
  .allDrivers ul {
    width: 200px;
    background-color: white;
    border: 1px solid transparent;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    padding-left: 0;
  }
  .allDrivers ul li {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    font-size: 16px;
    font-weight: bold;
    align-items: center;
  }
  .allDrivers ul li:hover {
    cursor: pointer;
    background-color: lightGray;
  }
  @media (min-width: 768px){
    .mapInformation{
      height: 92vh;
    }
    .dmarker {
      width: 32px;
    }
    .rmarker, .rmarkerAssign {
      width: 22px;
      height: 22px;
    }
    .circle {
      width: 8px;
      height: 8px;
    }
    .infoRBox, .infoDBox {
      width: 150px;
    }
    .infoRBox:before {
      left: 70px;
    }
    .infoDBox:before {
      left: 65px;
    }
    p, h5 {
      font-size: 14px;
    }
    span {
      font-size: 16px;
    }
    .allDrivers ul {
      width: 280px;
    }
    .allDrivers ul li {
      font-size: 24px;
    }
    .plusSign {
      width: 28px;
      height: 28px;
      font-size: 14px;
    }
  }
  @media (min-width: 1024px){
    .mapInformation {
      height: 100vh;
    }
    .nav {
      display: flex;
      z-index: 1;
    }

  }
